
import { defineComponent, computed } from 'vue'
import { ElMessageBox, ElMessage } from 'element-plus'
import axios from 'axios'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'

export default defineComponent({
  name: 'MeetingNavBar',
  props: {
    startSignal: Boolean,
  },
  setup(props, { emit }) {
    const startSignal = computed(() => props.startSignal)
    const store = useStore()
    const router = useRouter()
    const meetingId = computed(() => store.state.meeting.id)

    const closeMeeting = function () {
      axios.post(`/meeting/${meetingId.value}/close`, 
        { headers: { Authorization: `Bearer ${localStorage.getItem("token")}` }}
      ).then(res => {
        console.log(res)
        router.push({ name: 'Home'})
      }).catch(err => {
        console.log(err.response)
      })
    }

    const clickCloseMeeting = function () {
      ElMessageBox.confirm(
        '종료하시겠습니까?',
        '종료',
        {
          confirmButtonText: '종료',
          cancelButtonText: '취소',
          type: 'warning',
        }
      )
        .then(() => {
          emit('closeMeeting')
          closeMeeting()
          store.dispatch('deleteMeeting')
          router.push({ name: 'Home'})
          ElMessage({
            type: 'success',
            message: '종료되었습니다.',
          })
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: '취소되었습니다.',
          })
        })
    }
    const clickLeaveMeeting = function () {
      ElMessageBox.confirm(
        '방을 나가시겠습니까?',
        '방 나가기',
        {
          confirmButtonText: '나가기',
          cancelButtonText: '취소',
          type: 'warning',
        }
      )
        .then(() => {
          emit('leaveMeeting')
          store.dispatch('deleteMeeting')
          router.push({ name: 'Home'})
          ElMessage({
            type: 'success',
            message: '퇴장하셨습니다.',
          })
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: '취소되었습니다.',
          })
        })
    }
    return { clickCloseMeeting, clickLeaveMeeting, startSignal }
  }
})
